<template>
  <div class="dashboard">

    <newsfeed-info-widgets></newsfeed-info-widgets>

    <div class="row">
      <div class="col-md-8">
        <vuestic-widget class="" v-for="project in projectsData" :key="project.id">
          <div>
            <swiper :options="swiperOption" class="sliderStyle">
              <swiper-slide> <img id="imgStyle" src="https://www.register.com/imgs/productDetail/custom-website-design-v2.jpg"></swiper-slide>
              <swiper-slide><img id="imgStyle" src="https://www.register.com/imgs/productDetail/custom-website-design-v2.jpg"></swiper-slide>
              <swiper-slide><img id="imgStyle" src="https://www.register.com/imgs/productDetail/custom-website-design-v2.jpg"></swiper-slide>
              <swiper-slide><img id="imgStyle" src="https://www.register.com/imgs/productDetail/custom-website-design-v2.jpg"></swiper-slide>
              <swiper-slide><img id="imgStyle" src="https://www.register.com/imgs/productDetail/custom-website-design-v2.jpg"></swiper-slide>
              <div class="swiper-button-prev" slot="button-prev"></div>
              <div class="swiper-button-next" slot="button-next"></div>
            </swiper>
            <div id="projects-name-div">
              <span class="projects-name"><strong><a href="" @click.prevent="viewProject(project._id)">{{project.title}}</a> </strong></span><br>
              <span class="projects-time"><timeago :since="project.createdAt" :auto-update="60"></timeago></span>
            </div>
          </div>
          <div id="projects-content-div">
            <span id="projects-description">{{project.abstract}}</span>
            <!-- <a href="" class="viewMoreBtn" @click="viewProject(project._id)"> Read More <i class="fa fa-arrow-right"></i> </a>           -->
          </div>
          <div id="tagDiv">
            <strong>Tags:</strong><span v-for="tag in project.tags" :key="tag.id" class="tagNames">{{tag.name}}</span>
          </div>
          <hr>
          <div class="userInteractionDiv">
            <i id="spacingStyle" class="fa fa-thumbs-up">{{project.upvotes.length}}</i>{{upvoteContent(project.upvotes.length)}}
          </div>
        </vuestic-widget>
      </div>
      <div class="col-md-4 col-sm-12 sidenav">
         <vuestic-widget class="info-widget">
        <div class="ad-header">
          <span class="ad-header-title"> Hot topic around internet </span>
          <i class="fa fa-fire ad-header-icon"></i>
        </div>
        <div class="ad-content">
          <div class="ad-title"><a href="#">70% companies in Chennai are using Freshsales as their Customer Relationship software</a></div>
        </div>        

      </vuestic-widget>
        <vuestic-widget class="live-feed" headerText="Live feeds">
          <vuestic-feed class="newsfeed-page" :initialPosts="posts"></vuestic-feed>
        </vuestic-widget>
      </div>
    </div>


  </div>
</template>

<script>
  import VueDisqus from 'vue-disqus/VueDisqus.vue'
  import NewsfeedInfoWidgets from './NewsfeedInfoWidgets'

  export default {
    name: 'dashboard',
    components: {
      NewsfeedInfoWidgets,
      VueDisqus
    },
    data () {
      return {
        companyData: '',
        projectsData: '',
        swiperOption: {
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          }
        },
        posts: [
          {
            id: 0,
            photoURL: 'https://goo.gl/KnVxVY',
            name: 'Harishankar',
            text: 'registered for your contest SpriteXtreme',
            action: 'upvoted',
            by: 'Balaji',
            calender: '32m',
            post_desc: 'Happy to upload my final year project'
          },
          {
            id: 1,
            photoURL: 'https://goo.gl/1nKusR',
            name: 'Balaji D Loganathan',
            text: 'was given an award by a company',
            action: 'commented',
            by: 'Surendran',
            calender: '3h',
            post_desc: 'Uploaded my dream project'
          },
          {
            id: 2,
            photoURL: 'https://goo.gl/Ckaexc',
            name: 'Surendran S',
            text: 'has the most upvoted project which you upvoted',
            action: 'upvoted',
            by: 'HS',
            calender: '2d',
            post_desc: 'Woohoo!!!! Finally uploaded the project ^_^'
          }
        ]
      }
    },

    methods: {
      giveInitial: function (post) {
        let initial = post.name.charAt(0)
        return initial
      },
      viewProject: function (projectId) {
        this.$router.push('/company/project/' + projectId)
      },
      upvoteContent: function (len) {
        if (len < 2) {
          return 'upvote'
        } else {
          return 'upvotes'
        }
      }
    },
    created () {
      const email = this.$ls.get('email')
      const token = this.$ls.get('token')
      const self = this
      const header = {
        headers: {
          'Authorization': 'Bearer ' + token
        }
      }
      this.$http.get('/companies/get?email=' + email, header)
      .then((companyData) => {
        console.log('company Data', companyData.data)
        self.companyData = companyData.data
        self.$http.get('/projects/all', header)
        .then(function (projectsData) {
          console.log('projects data', projectsData.data.projects)
          self.projectsData = projectsData.data.projects
        })
        .catch(function (projectsDataErr) {
          console.log('projects data err', projectsDataErr)
        })
      }).catch((companyErr) => {
        console.log('company err', companyErr)
      })
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../sass/_variables.scss";
  .newsfeed-page{
    padding-left: 0rem !important;
  }
  .feed-card-time{
    margin-top: 3px;
    color: #a29e9e;
  }
  #feed-card-image{
    display: inline-block;
    border: 1px solid #a29e9e;
    border-radius: 50%;
    background: #CA3C25;
    font-size: 1.5rem;
    height: 3rem;
    width: 3rem;
    padding-left: 0.95rem;
    padding-top: 0.3rem;
    color: #e8dfdf;
    vertical-align: top;
  }
  .sidenav{
    position: fixed;
    margin-left: 720px;
    width: 26%;
    top: 13.7%;
  }
  @media screen and (max-width: 650px)
  {
    .sidenav{
    position: relative !important;
    margin-left: 0px !important;
    width: 100% !important;
    }
  }
  #feed-card-user-name-div{
    display: inline-block;
    margin-left: 0.4rem;
  }

  #post-content-div{
    margin-top: 0.5rem;
  }

  #tagDiv {
    display: inline-block;
    margin-top: 1rem;
  }
  .tagNames {
    padding: 0.2rem 0.5rem;
    margin-left: 0.5rem;
    background: $tagcolor;
    color: white;
    border-radius: 5%;
  }
  .projects-time{
    margin-top: 3px;
    color: #a29e9e;
  }
  
  #projects-name-div{
    display: inline-block;
    // margin-left: 0.4rem;
  }

  .projects-name{
    font-size: 1.3rem;
  }

  #projects-content-div{
    margin-top: 0.5rem;
  }

  .viewMoreBtn {
    display: block;
  }
  #spacingStyle{
    letter-spacing: 5px;
  } 
  .ad-header-title{
    display: inline-block;
    font-size: 1em; 
  }
  .ad-header-icon{
    font-size: 2em;
    float: right;
  }

  .ad-header{
    padding-bottom: 2%;
  }

  .ad-title{
    font-size: 1.1em;
  }
  .info-widget-inner {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;

    &.has-chart {
      justify-content: space-between;
    }

    .stats {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100%;
    }
  }

  .stats-number {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 2.625rem;
    margin-bottom: 0.5rem;

    .stats-icon {
      font-size: 1.5625rem;
      position: absolute;
      top: 0.625rem;
      left: -1.25rem;

      &.icon-wide {
        left: -1.875rem;
      }
    }
  }
  .sliderStyle{
    margin-bottom: 3%;
    height: 300px;
    width: 100%;
    z-index: 1;
  }
</style>



